#point{
    padding: 140px 0;
    background-color: #EBF5FF;
    background-image: url(../images/common/slash_01.svg);
    background-repeat: no-repeat;
    background-position: top 50px right 0;
    background-size: 245px;
}

.point_title{
    text-align: center;
}

.point_content{
    max-width: 1100px;
    margin: 0 auto;
}

.point_item{
    display: flex;
    column-gap: 80px;
    margin-top: 130px;
}

.point_item:nth-child(1){
    margin-top: 80px;
}

.point_item img{
    width: 51%;
    border-radius: 20px;
}

.point_item_text{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: start;
    padding-bottom: 20px;
}

.point_item span{
    font-size: 70px;
    line-height: 1;
    letter-spacing: 0.06rem;
}

.point_item h3{
    display: inline-block;
    font-size: 30px;
    line-height: 1;
    letter-spacing: 0.03rem;
    margin-top: 10px;
    padding: 0.4em 0.6em;
}

.point_item p{
    margin-top: 20px;
    font-size: 16px;
    line-height: 1.75;
    text-align: justify;
}